* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  color: #000 !important;
  text-decoration: none;
}

.authMain {
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(9, 9, 121, 1) 11%,
    rgba(0, 212, 255, 1) 100%
  );
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  overflow: auto;
}

.auth-page {
  background: white;
  padding: 40px;
  margin: auto;
  text-align: center;
  border-radius: 30px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 3);
}

.auth-form_subtitle {
  color: rgb(0, 128, 255);
}

.auth-form__form {
  margin: auto;
  text-align: center;
  width: 300px;
  height: 280px;
}

.auth-logo {
  padding-bottom: 20px;
}

.reg-form__form {
  margin: auto;
  text-align: center;
  width: 300px;
  height: 500px;
}

.edit-form__form {
  margin: auto;
  text-align: center;
  width: 800px;
  height: 630px;
}

.tableMain {
  background: white;
  padding: 10px;
  width: 80%;
  margin: auto;
  text-align: center;
  border-radius: 30px 30px 30px 30px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 3);
}

.tableMainHeader {
  background: rgb(5, 100, 181);
  display: flex;
  justify-content: space-between;
  padding: 20px;
  margin: auto;
  text-align: center;
  border-radius: 0px 0px 0px 0px;
  margin-bottom: 40px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 3);
}

.tableMainFooter {
  background: white;
  display: flex;
  justify-content: center;
  padding: 20px;
  margin: auto;
  text-align: center;
}

.table-page {
  width: 97%;
  height: 97%;
  background: white;
  padding: 40px;
  margin: auto;
  text-align: center;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 5);
}

.editForm {
  background: white;
  padding: 10px;
  width: 80%;
  margin: auto;
  text-align: center;
  border-radius: 30px 30px 30px 30px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 3);
}

.alert-enter {
  opacity: 0;
  transform: scale(0.7);
}
.alert-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 1000ms, transform 1000ms;
}
.alert-exit {
  opacity: 1;
}
.alert-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}
